import { app } from "../lib/app";

export default {
  addToCart() {
    let cartItem = {
      id: parseInt(Math.random() * 100, 10),
      name: "Giày Nike Jordan 1 cổ thấp",
      image: "images/products/nike-1.png",
      price: 3200000,
      quantity: 2,
    };

    app.cartVisible = true;

    app.cart.unshift(cartItem);

    localStorage.setItem("cart", JSON.stringify(app.cart));
  },
  removeCartItem(removeItem) {
    let cart = app.cart;

    for (let i = 0; i < cart.length; i++) {
      let item = cart[i];
      if (item.id === removeItem.id) {
        cart = cart.splice(i, 1);
      }
    }

    localStorage.setItem("cart", JSON.stringify(app.cart));
  },
};
